import React from 'react';
import set from 'lodash/set'
import { Formik, Form, FieldArray, ErrorMessage } from 'formik'
import { object, string, array, setLocale } from 'yup';
import mapValues from 'lodash/mapValues'

import Box from '../../components/Box'
import Button from '../../components/Button'
import Text from '../../components/Text'
import CheckBox from '../../components/CheckBox'
import Flex from '../../components/Flex'
import Modal from '../../components/Modal'
import { mobileOrDesktop } from '../../components/ThemeProvider/theme';
import storeForm from '../../services/googleSheets/storeForm'
import FormikField from './FormikField'

setLocale({
  mixed: {
    required: '',
  },
  string: {
    email: 'E-mail',
  },
})

const datas = ['', '', '', '', '', 'Email']
const interestings = ['', '/', '', '', '', '']

const shape = datas.reduce((s, d, i, { length }) => {
  return set(s, `n${i}`, i === length - 1 ? string().email().required() : string().required());
}, {
  interest: array().of(string()).required(),
});

const schema = object().shape(shape)

const Action = ({
  isOpen,
  formIsStored,
  onRequestClose,
  requestFormStorage,
  rowId,
}) => {
  return (
    <Modal
      isOpen={isOpen}
      onRequestClose={onRequestClose}
    >
      {formIsStored ? (
        <Box p="2em">
          <Text fontSize="2em"></Text>
        </Box>
      ) : (
        <Formik
          validationSchema={schema}
          initialValues={{ ...mapValues(shape, () => ''), interest: [] }}
          onSubmit={(values, { setSubmitting }) => {
            setSubmitting(true);
            requestFormStorage(Object.keys(shape).map((k) => k === 'interest' ? values.interest.join() : values[k]).concat(rowId));
          }}
        >
          {({ values }) => (
            <Box px={mobileOrDesktop('0', '2em')}>
              <Box align="center" borderBottom="1px solid" pb="1em">
                <Text fontSize={mobileOrDesktop('1.5em', '2em')} my="0.5em"></Text>
                <Text></Text>
              </Box>
              <Form>
                <Box fontSize={mobileOrDesktop('0.8em', '1em')}>
                  <Flex my={mobileOrDesktop('1em', '2em')} flexWrap="wrap" justifyContent="space-between">
                    {datas.map((d, i) => (
                      <Box p={mobileOrDesktop('0.5em', '0.75em')} key={i} width={mobileOrDesktop(1, 1 / 2)}>
                        <FormikField textAlign="right" labelWidth="3em" label={d} name={`n${i}`} />
                      </Box>
                    ))}
                  </Flex>
                  <Box pl="0.5em">
                    <Text>()</Text>
                    <Text color="red" fontSize="0.8em">
                      <ErrorMessage name="interest" />
                    </Text>
                  </Box>
                  <FieldArray name="interest">
                    {(arrayHelpers) => (
                      <Flex my="0.5em" flexWrap="wrap" justifyContent="flex-start">
                        {interestings.map((int, i) => (
                          <Box p={mobileOrDesktop('0.5em', '0.75em')} key={i} width={mobileOrDesktop(1 / 2, 1 / 3)}>
                            <CheckBox
                              labelWidth="10em"
                              label={int}
                              name="interest"
                              value={int}
                              checked={values.interest.includes(int)}
                              onChange={e => {
                                if (e.target.checked) arrayHelpers.push(int);
                                else {
                                  const idx = values.categoryIds.indexOf(int);
                                  arrayHelpers.remove(idx);
                                }
                              }}
                            />
                          </Box>
                        ))}
                      </Flex>
                    )}
                  </FieldArray>
                </Box>
                <Text fontSize="0.75em" lineHeight="1.5"></Text>
                <Box textAlign="center" my="1.5em"><Button type="submit" py="0.5em" px="1.5em"></Button></Box>
              </Form>
            </Box>
          )}
        </Formik>
      )}
    </Modal>
  );
};

export default storeForm(Action);
